import React, {useEffect, useReducer, useState} from 'react';
import {ChoixComponent} from "./ChoixComponent";
import {ChoixDTO} from "../models/dtos/ChoixDTO";
import {useAppDispatch, useAppSelector} from "../../../shared/store/hooks";
import {getChoix} from "../slices/choix.slice";
import {QuestionDTO} from "../models/dtos/QuestionDTO";

interface QuestionComponentProps {
    currentQuestion: QuestionDTO
    hasChoisis: ()=> void
}

export const QuestionComponent = ({currentQuestion, hasChoisis}: QuestionComponentProps) => {
    const choixData = useAppSelector((state)=> state.choix.value);
    const dispatch = useAppDispatch();
    const [style, setStyle] = useState<object>({})
    const [, forceUpdate] = useReducer(x => x + 1, 0);

    useEffect(() => {
        // On retrouve la valeur dja choisis par l'utilisateur pour l'initialiser
        if(choixData.length > 0) {
            let choixDtos = choixData.map(choixMapper);
            console.log('map', currentQuestion.choix.map(y => y.id))
            console.log('map2', choixDtos.map(y => y.id))
            let choixSelected = choixDtos.find(x => currentQuestion.choix.map(y => y.id).includes(x.id));
            console.log('choixSelected', choixSelected)

            if(choixSelected) {
                let index = currentQuestion.choix.indexOf(choixSelected);

                if(index !== -1)
                    currentQuestion.choix[index] = choixSelected;
            }
        }
    }, []);

    // A DEPLACER DANS UNE Factory
    function transformToPlainObject(dto: ChoixDTO) {
        return { ...dto };
    }

    const choixMapper = (choixDto: ChoixDTO) => {
        const {id, libelle, typeChoix, nextQuestion, selected} = choixDto;
        console.log('dto', choixDto);
        return choixDto as ChoixDTO;
    }

    const handleRadioChecked = (value: ChoixDTO, isChecked: boolean)=> {
        // je vire l'ancienne valeur du tableau
        let oldchoix = choixData;
        let valeursASupprimer = currentQuestion.choix.filter(x => x.id !== value.id).map((x)=> x.id);
        let newChoix = oldchoix.filter(c => !valeursASupprimer.includes(c.id));

        // j'ajoute la nouvelle
        newChoix.push(new ChoixDTO(value.id, value.libelle, true, value.typeChoix));

        // je remets  jour le tableau
        if(isChecked) {
            let choixSerializable = newChoix.map(x => transformToPlainObject(x))
            dispatch(getChoix(choixSerializable));
            hasChoisis();
        }
    }

    return (
        <div className="question">
            <h1 className="question-libelle">{currentQuestion.libelle}</h1>
            {
                currentQuestion.choix && currentQuestion.choix.map((choix)=> (
                        <ChoixComponent
                            key={choix.id}
                            choix={choix}
                            onRadioChecked={handleRadioChecked}
                        ></ChoixComponent>
                ))
            }
        </div>
    );
};