import {createSlice} from "@reduxjs/toolkit";
import {TypePeauEnum} from "../models/Enums/TypePeauEnum";
import {TextureEnum} from "../models/Enums/TextureEnum";

interface FilterElementsState {value: (TypePeauEnum | TextureEnum)[]}
const initialState: FilterElementsState = {value: []}

export const querySlice = createSlice({
    name: "filterElements",
    initialState,
    reducers: {
        getFilterElements: (state, {payload}) => {
            state.value = payload
        }
    }
});

export const {getFilterElements} = querySlice.actions;
export default querySlice.reducer;