import React, { useEffect, useRef, useState } from 'react';
import { IDiagnostiqueManager } from "../services/IDiagnostiqueManager";
import { IDynamicConfigurationService } from "../../../shared/services/configurations/IDynamicConfigurationService";
import { DynamicConfigurationEnum } from "../../../shared/models/configurations/DynamicConfigurationEnum";
import { QuestionDTO } from "../models/dtos/QuestionDTO";
import { AppStepper, AppStepperRef } from './AppStepper';
import { QuestionComponent } from "./QuestionComponent";
import { RessourceNotFoundException } from "../../../shared/exceptions/RessourceNotFoundException";
import {useLocation, useNavigate} from "react-router-dom";

interface DiagnostiqueFormulaireComponentProps {
    diagnostiqueManager: IDiagnostiqueManager,
    configurationManager: IDynamicConfigurationService
}

export const DiagnostiqueFormulaireComponent = ({ configurationManager, diagnostiqueManager }: DiagnostiqueFormulaireComponentProps) => {
    const [questions, setQuestions] = useState<QuestionDTO[]>([]);
    const [currentQuestion, setCurrentQuestion] = useState<QuestionDTO>();
    const appStepperRef = useRef<AppStepperRef>(null);

    const navigate = useNavigate();
    const { search } = useLocation();
    const searchParams = new URLSearchParams(search);
    const [page, setPage] = useState<number>(1);

    //TODO: grer logique du choix "autres"
    //TODO: on ne peut pas faire avancer le stepper si un choix a deja t selected
    //TODO: FRONT

    useEffect(() => {
        const fetchQuestions = async () => {
            try {
                const fetchedQuestions = await configurationManager.GetDataAsync<QuestionDTO[]>(DynamicConfigurationEnum.QuestionData);
                setQuestions(fetchedQuestions);
            } catch (e) {
                throw new RessourceNotFoundException("Impossible de rcuprer les questions");
            }
        };
        fetchQuestions().catch(console.error);
    }, [configurationManager]);

    useEffect(() => {
        const pageParam = searchParams.get('page');
        if(pageParam){
            let currentPage = parseInt(pageParam.toString());
            if(currentPage <= 0){
                navigate(`?page=1`);
                return;
            }
            setPage(currentPage);
            navigate(`?${searchParams.toString()}`);
        } else {
            setPage(1)
            navigate(`?page=1`);
        }
    }, [search]);

    const goToPage = (pageNum: number) => {
        if(pageNum <= 0 || pageNum > questions.length){
            return;
        }
        searchParams.set('page', pageNum.toString());
        setCurrentQuestion(questions[pageNum - 1]);
        navigate(`?${searchParams.toString()}`);
    };

    const goToQuestion = (question: QuestionDTO | undefined) => {
        if(question) {
            setCurrentQuestion(question);
            const pageParam = searchParams.get('page');
            if(pageParam) {
                let currentPage = parseInt(pageParam.toString());
                searchParams.set('page', question.ordre.toString());
                navigate(`?${searchParams.toString()}`);
            }
        }
    };

    useEffect(() => {
        console.log('render');
        if (questions.length > 0) {
            setCurrentQuestion(questions[page ? page - 1 : 0]);
        }
    }, [questions]);

    /**
     * Mthode pour incrmenter le Stepper et passer  la step suivante
     */
    const incrementStepper = () => {
        if (appStepperRef.current) {
            appStepperRef.current.incrementStepper();
            if(currentQuestion){
                let currentChoix = currentQuestion?.choix.find(x => x.selected);
                if(currentChoix && currentChoix.nextQuestion) {
                    let nextQuestion = questions.find(x => x.id === currentChoix?.nextQuestion)
                    questions.splice(currentQuestion.ordre, 1)
                    console.log(questions)
                    goToQuestion(nextQuestion);
                }
            }
            goToPage(page + 1);
        }
    };

    return (
        <main>
            <AppStepper ref={appStepperRef} totalSteps={questions.length} />
            {
                currentQuestion &&
                <QuestionComponent key={currentQuestion.id} currentQuestion={currentQuestion} hasChoisis={incrementStepper}/>
            }
        </main>
    );
};
