import React from 'react';
import {NavLink} from "react-router-dom";
import {ProduitDTO} from "../models/ProduitDTO";

interface ProduitCardProps {
    produit: ProduitDTO
}

export const ProduitCardComponent = ({ produit }: ProduitCardProps) => {
    return (
        <NavLink to={produit.lien}>
            <div className="product-card" style={{ backgroundImage: `url(${produit.image})` }}>
                <div></div>
                <div className="product-infos">
                    <h5>{produit.nom}</h5>
                    <p>{produit.prix}</p>
                </div>
            </div>
        </NavLink>
    );
};