import { IDynamicConfigurationService } from "./IDynamicConfigurationService";
import { DynamicConfigurationEnum } from "../../models/configurations/DynamicConfigurationEnum";
import { RessourceNotFoundException } from "../../exceptions/RessourceNotFoundException";
import axios from 'axios';
import {TextureEnum} from "../../../features/produits/models/Enums/TextureEnum";

export class DynamicConfigurationService implements IDynamicConfigurationService {
    async GetDataAsync<T>(dataType: DynamicConfigurationEnum): Promise<T> {
        let url = '';
        let object= {};

        switch (dataType) {
            case DynamicConfigurationEnum.TextureData:
                url = './data/texturesData.json';
                break;
            case DynamicConfigurationEnum.TypePeauData:
                url = './data/typesPeauData.json';
                break;
            case DynamicConfigurationEnum.QuestionData:
                url = './data/questionsData.json';
                break;
            case DynamicConfigurationEnum.ProduitData:
                url = './data/produitsData.json';
                break;
            default:
                throw new RessourceNotFoundException("La ressource demande n'a pas t trouve en configuration");
        }

        try {
            const response = await axios.get(url);
            return response.data as T;
        } catch (error) {
            throw error;
        }
    }
}
