import React, {CSSProperties, useEffect, useId, useState} from 'react';
import {ChoixDTO} from "../models/dtos/ChoixDTO";
import {TypeChoixEnum} from "../models/enums/TypeChoixEnum";

interface ChoixComponentProps {
    style?: CSSProperties,
    choix: ChoixDTO,
    onRadioChecked: (choix: ChoixDTO, isChecked: boolean)=> void
}

export const ChoixComponent = ({style, choix, onRadioChecked}: ChoixComponentProps) => {
    const id = useId();
    const [isInCustomMode, setIsInCustomMode] = useState(false);

    const onChecked = (choix: ChoixDTO, isChecked: boolean)=> {
        if(choix.typeChoix === TypeChoixEnum.CUSTOM && isChecked) {
            setIsInCustomMode(true);
        } else {
            onRadioChecked(choix, isChecked)
        }
    }

    useEffect(() => {
        if(choix.typeChoix === TypeChoixEnum.CUSTOM && choix.selected) {
            setIsInCustomMode(true);
        }
    }, []);

    const onBlur = (value: string)=> {
        let newChoix = new ChoixDTO(choix.id, value, choix.selected, choix.typeChoix)
        onRadioChecked(newChoix, true)
    }

    return (
        <div className="choix-box" style={style}>
            <input id={id} name="choix" className="choix-checkbox" type="radio" checked={choix.selected} onChange={(e: React.ChangeEvent<HTMLInputElement>)=> onChecked(choix, e.target.checked)}/>
            <label htmlFor={id} className="choix">
            {
                isInCustomMode ?
                    <input className="custom-input" type="text" autoFocus
                           onBlur={(e)=> onBlur(e.target.value)}
                           defaultValue={choix.libelle !== "autres" ? choix.libelle : "" }
                    />
                        :
                choix.libelle
                }
            </label>
        </div>
    );
};