import {createSlice} from "@reduxjs/toolkit";
import {ChoixDTO} from "../models/dtos/ChoixDTO";

export interface ChoixState {value: ChoixDTO[]}
export const initialState: ChoixState = {value: []}

export const choixSlice = createSlice({
    name: "choix",
    initialState,
    reducers: {
        getChoix: (state, {payload}) => {
            state.value = payload
        }
    }
});

export const {getChoix} = choixSlice.actions;
export default choixSlice.reducer;