import React, {useEffect, useState} from 'react';
import {IProduitManager} from "../../features/produits/services/IProduitManager";
import {ProduitCardComponent} from "../../features/produits/components/ProduitCardComponent";
import {ProduitDTO} from "../../features/produits/models/ProduitDTO";
import {isEmpty} from "../helpers/IsEmpty";
import {ProduitFilter} from "../../features/produits/models/ProduitFilter";
import {LoaderListingComponent} from "../components/LoaderComponent";
import {SidebarComponent} from "../../features/produits/components/SidebarComponent";
import {IDynamicConfigurationService} from "../services/configurations/IDynamicConfigurationService";
import {DynamicConfigurationEnum} from "../models/configurations/DynamicConfigurationEnum";
import {TextureEnum} from "../../features/produits/models/Enums/TextureEnum";
import {TypePeauEnum} from "../../features/produits/models/Enums/TypePeauEnum";
import {RessourceNotFoundException} from "../exceptions/RessourceNotFoundException";
import {useAppSelector} from "../store/hooks";

interface ProductListingLayoutProps {
    produitManager: IProduitManager,
    configurationService: IDynamicConfigurationService,
}

export const ProduitListingLayout = ({produitManager, configurationService}: ProductListingLayoutProps) => {

    const filterElements = useAppSelector((state)=> state.filterElements.value);

    const [loading, setLoading] = useState<boolean>(true);
    const [pageSize, setPageSize] = useState<number>(0);
    const [produits, setProduits] = useState<ProduitDTO[]>();
    const [filter, setFilter] = useState<ProduitFilter>();
    const [textures, setTextures] = useState<TextureEnum[]>();
    const [typesPeau, setTypesPeau] = useState<TypePeauEnum[]>();

    useEffect(() => {
        const fetchProduits = async(elements: (TextureEnum | TypePeauEnum)[] | undefined) => {
            try {
                let filterDto = elements ? buildProduitFilterByArray(elements) : undefined;
                const productsData = await produitManager.GetAllFilteredProduits(filterDto);
                setTextures(await configurationService.GetDataAsync<TextureEnum[]>(DynamicConfigurationEnum.TextureData));
                setTypesPeau(await configurationService.GetDataAsync<TypePeauEnum[]>(DynamicConfigurationEnum.TypePeauData));
                setPageSize(elements ? elements.length : 12);
                setProduits(productsData);
            } catch (error) {
                console.error('Une erreur s\'est produite lors de la rcupration des produits', error);
            } finally {
                setLoading(false);
            }
        }
        fetchProduits(filterElements).catch(console.error);
    }, [filterElements]);

    const buildProduitFilterByArray = (elements: (TextureEnum | TypePeauEnum)[])=> {
        let filterDto: ProduitFilter | undefined = new ProduitFilter([], []);

        if(!elements)
            return;

        for(let element of elements) {
            if (Object.values(TextureEnum).includes(element as TextureEnum))
                filterDto.textureCriteria.push(element as TextureEnum)
            else if (Object.values(TypePeauEnum).includes(element as TypePeauEnum))
                filterDto.typePeauCriteria.push(element as TypePeauEnum)
            else
                throw new RessourceNotFoundException(`La valeur ${element} ne fais pas partie des critres de produit`)
        }
        return filterDto;
    }

    return (
        <main className="products-listing-section">
            <div className="title">
                <h1>Liste des produits</h1>
            </div>
            <SidebarComponent items={{typesPeau: typesPeau, textures: textures}} />
            <div className="products-listing">
                {loading ? (
                    <LoaderListingComponent itemCount={pageSize} />
                ) : !isEmpty(produits) ? (
                    produits && produits
                        .map((produit: ProduitDTO) => (
                        <ProduitCardComponent key={produit.id} produit={produit} />
                    ))
                ) : (
                    <h1>Aucun produit trouv :(</h1>
                )}
            </div>
        </main>
    );
};