import React, {useId, useRef, useState} from 'react';
import {useClickOutside} from "../hooks/useOutsideAlerter";
import {Checkbox} from '@mui/material';
import {TextureEnum} from "../models/Enums/TextureEnum";
import {TypePeauEnum} from "../models/Enums/TypePeauEnum";
import {useAppDispatch, useAppSelector} from "../../../shared/store/hooks";
import {getFilterElements} from "../slices/filterElements.slice";

interface SidebarComponentProps {
    items: { textures: TextureEnum[] | undefined, typesPeau: TypePeauEnum[] | undefined },
}

export const SidebarComponent = ({ items }: SidebarComponentProps) => {

    const [isMenuOpen, setIsMenuOpen] = useState<boolean>(false);
    const sidebarRef = useRef<HTMLDivElement>(null);

    const toggleSidebar = ()=> {
        setIsMenuOpen((prevIsMenuOpen)=> prevIsMenuOpen = !prevIsMenuOpen);
    }

    const onClose = ()=> {
        if(isMenuOpen)
            setIsMenuOpen(false);
    }

    useClickOutside(sidebarRef, onClose);

    return (
        <div ref={sidebarRef}>
            <button className={`hamburger ${isMenuOpen ? 'open' : ''}`} onClick={toggleSidebar}>
                <span className="bar"></span>
                <span className="bar"></span>
                <span className="bar"></span>
            </button>
            <div className={`sidebar ${isMenuOpen ? 'open' : ''}`}>
                <div className="sidebar-element">
                    <h4 className="sidebar-title">Type de peau</h4>
                    {
                        items.typesPeau?.map((typePeau)=> (
                            <SidebarElement key={typePeau} value={typePeau} />
                        ))
                    }
                </div>
                <div className="sidebar-element">
                    <h4 className="sidebar-title">Texture</h4>
                    {
                        items.textures?.map((texture)=> (
                            <SidebarElement key={texture} value={texture} />
                        ))
                    }
                </div>
            </div>
        </div>
    );
};

interface SidebarElementProps {
    value: TypePeauEnum | TextureEnum,
}

const SidebarElement = ({value}: SidebarElementProps) => {
    const dispatch = useAppDispatch();
    const filterElements = useAppSelector((state)=> state.filterElements.value);
    const id = useId();

    const handleChecked = (isChecked: boolean, value: TypePeauEnum | TextureEnum)=> {
        let elements: (TextureEnum | TypePeauEnum)[];
        if(isChecked){
            elements = [...filterElements, value]
        }
        else {
            elements = [...filterElements]
            const index = elements.indexOf(value);
            elements.splice(index, 1);
        }
        dispatch(getFilterElements(elements));
    }

    return (
        <div className="sidebar-children">
            <Checkbox id={id} size="large" onChange={(e: React.ChangeEvent<HTMLInputElement>)=> handleChecked(e.target.checked, value)}/>
            <label className="sidebar-children-label" htmlFor={id}>{value.toString()}</label>
        </div>
    );
};

