import React, {forwardRef, useImperativeHandle, useState} from 'react';

type AppStepperProps = {
    totalSteps: number
}

export interface AppStepperRef {
    incrementStepper: () => void
}

export const AppStepper = forwardRef<AppStepperRef, AppStepperProps>((props: AppStepperProps, ref) => {
    const [currentStep, setCurrentStep] = useState<number>(1);

    const appStepperDynamicStyle: object = {
        transition: "width 300ms ease-out",
        width: `${Math.floor((currentStep / props.totalSteps) * 100)}%`,
    };

    useImperativeHandle(ref, () => ({
        incrementStepper() {
            setCurrentStep(
                // On s'assure que la step courante ne peux pas dpasser le nombre total de step
                (prevCurrentStep) =>
                    prevCurrentStep < props.totalSteps ? prevCurrentStep + 1 : prevCurrentStep
            )
        }
    }));

    return (
        <div className="stepper">
            <div className="stepper-item" style={appStepperDynamicStyle}>{`${currentStep}/${props.totalSteps}`}</div>
        </div>
    );
});