import React, {useEffect, useState} from 'react';
import {NavLink} from "react-router-dom";
import {ProduitCardComponent} from "../../features/produits/components/ProduitCardComponent";
import {ProduitDTO} from "../../features/produits/models/ProduitDTO";
import {IProduitManager} from "../../features/produits/services/IProduitManager";
import {LoaderListingComponent} from "../components/LoaderComponent";

interface HomeLayoutProps {
    manager: IProduitManager
}

export const HomeLayout = ({manager}: HomeLayoutProps) => {

    const [loading, setLoading] = useState<boolean>(true);
    const [pageSize, setPageSize] = useState<number>(0);
    const [products, setProducts] = useState<ProduitDTO[]>();

    useEffect(() => {
        const getProductsData = async () => {
            try {
                const productsData = await manager.GetHomeProduits();
                setProducts(productsData);
                setPageSize(3);
            } catch (error) {
                console.error('Une erreur s\'est produite lors de la rcupration des produits', error);
            } finally {
                setLoading(false);
            }
        };
        getProductsData().catch(console.error);
    }, []);

    return (
        <main className="home-section">
            <div className="title">
                <h1>Bienvenue !</h1>
            </div>
            <div className="products-listing">
                {loading ? (
                    <LoaderListingComponent itemCount={pageSize} />
                ) : (
                    <>
                        {products?.find(x => x.nom === "Crme Anti-Pollution") && <ProduitCardComponent produit={products.find(x => x.nom === "Crme Anti-Pollution")!} />}
                        {products?.find(x => x.nom === "Gel Nettoyant Oxygnant") && <ProduitCardComponent produit={products.find(x => x.nom === "Gel Nettoyant Oxygnant")!} />}
                        {products?.find(x => x.nom === "Soin Matifiant Homme") && <ProduitCardComponent produit={products.find(x => x.nom === "Soin Matifiant Homme")!} />}
                    </>
                )}
            </div>
            <NavLink className="products-link" to="/produits">Voir tous les produits</NavLink>
            <div className="diagnostic-section">
                <div><h2>Votre diagnostique</h2></div>
                <NavLink className="button-diag" to="/mon-diagnostique?page=1">Dcouvrir <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path d="M438.6 278.6c12.5-12.5 12.5-32.8 0-45.3l-160-160c-12.5-12.5-32.8-12.5-45.3 0s-12.5 32.8 0 45.3L338.8 224 32 224c-17.7 0-32 14.3-32 32s14.3 32 32 32l306.7 0L233.4 393.4c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0l160-160z"/></svg></NavLink>
            </div>
        </main>
    );
};
