import {TextureEnum} from "./Enums/TextureEnum";
import {TypePeauEnum} from "./Enums/TypePeauEnum";

export class ProduitFilter {
    private _textureCriteria?: TextureEnum[];
    private _typePeauCriteria?: TypePeauEnum[];

    constructor(textureCriteria?: TextureEnum[], typePeauCriteria?: TypePeauEnum[]) {
        this._textureCriteria = textureCriteria;
        this._typePeauCriteria = typePeauCriteria;
    }

    get textureCriteria(): TextureEnum[] {
        return this._textureCriteria as TextureEnum[];
    }

    set textureCriteria(value: TextureEnum[]) {
        this._textureCriteria = value;
    }

    get typePeauCriteria(): TypePeauEnum[] {
        return this._typePeauCriteria as TypePeauEnum[];
    }

    set typePeauCriteria(value: TypePeauEnum[]) {
        this._typePeauCriteria = value;
    }
}