import { RefObject, useEffect } from "react";

/**
 * Hook qui dtecte les clics  l'extrieur d'un lment spcifi et appelle une fonction de rappel en rponse.
 * Cela peut tre utile pour fermer un modal, un menu droulant, ou d'autres lments interactifs lorsqu'un clic en dehors est dtect.
 *
 * @param ref RefObject pointant vers l'lment DOM  surveiller.
 * @param onOutsideClick Fonction  appeler lorsque un clic en dehors de l'lment rfrenc est dtect.
 */
export function useClickOutside(ref: RefObject<HTMLElement>, onOutsideClick: () => void): void {
    useEffect(() => {
        if (typeof window !== "undefined") {
            const handleClickOutside = (event: MouseEvent) => {
                if (ref.current && !ref.current.contains(event.target as Node)) {
                    onOutsideClick();
                }
            };

            document.addEventListener('mousedown', handleClickOutside);
            return () => {
                document.removeEventListener('mousedown', handleClickOutside);
            };
        }
    }, [ref, onOutsideClick]);
}
