/**
 * Vrifie si la valeur fournie est considre comme "vide".
 *
 * Une valeur est considre comme vide si elle correspond  l'un des critres suivants :
 * - Elle est `undefined`.
 * - Elle est `null`.
 * - Elle est une chane de caractres et, aprs suppression des espaces blancs, elle est de longueur zro.
 * - Elle est un objet et n'a aucune proprit propre (Note : cela inclut les objets littraux, mais pas les instances de classes personnalises qui n'ont pas de proprit numrable).
 * - Elle est un tableau et sa longueur est zro.
 * - Elle est une instance de `Map` ou `Set` et sa taille est zro.
 *
 * Note : Cette fonction ne considre pas les valeurs boolennes `false`, les nombres `0`, les dates ou d'autres types primitifs comme tant "vides",
 * mme si, dans certains contextes, ils pourraient tre considrs comme reprsentant une absence de valeur.
 *
 * @param value La valeur  tester.
 * @returns `true` si la valeur est considre comme vide selon les critres ci-dessus, sinon `false`.
 */
export const isEmpty = (value: any) => {
    return (
        value === undefined ||
        value === null ||
        (typeof value === "string" && value.trim().length === 0) ||
        (typeof value === "object" && Object.keys(value).length === 0) ||
        (Array.isArray(value) && value.length === 0) ||
        (value instanceof Map && value.size === 0) ||
        (value instanceof Set && value.size === 0)
    );
};