import {IProduitManager} from "./IProduitManager";
import {ProduitDTO} from "../models/ProduitDTO";
import {ProduitNotFoundException} from "../exceptions/ProduitNotFoundException";
import {ProduitFilter} from "../models/ProduitFilter";
import {TextureEnum} from "../models/Enums/TextureEnum";
import {IDynamicConfigurationService} from "../../../shared/services/configurations/IDynamicConfigurationService";
import {DynamicConfigurationEnum} from "../../../shared/models/configurations/DynamicConfigurationEnum";

export class ProduitManager implements IProduitManager {
    private _dynamicConfigurationService: IDynamicConfigurationService;

    constructor(dynamicConfigurationService: IDynamicConfigurationService) {
        this._dynamicConfigurationService = dynamicConfigurationService;
    }

    async GetProduitById(id: string): Promise<ProduitDTO> {
        if(id){
            let produitsData = await this._dynamicConfigurationService.GetDataAsync<ProduitDTO[]>(DynamicConfigurationEnum.ProduitData);
            let [produit] = await Promise.all([produitsData.find(x => x.id === id)]);
            if(produit) {
                return produit;
            }
            throw new ProduitNotFoundException("Le produit est introuvable");
        }
        throw new ProduitNotFoundException("L'id est null");
    }

    async GetHomeProduits(): Promise<ProduitDTO[]> {
        let produits: ProduitDTO[] = [];
        let produitsData = await this._dynamicConfigurationService.GetDataAsync<ProduitDTO[]>(DynamicConfigurationEnum.ProduitData);

        const cremeAntiPollution = produitsData.find(x => x.nom === "Crme Anti-Pollution");
        const gelNettoyantOxygenant = produitsData.find(x => x.nom === "Gel Nettoyant Oxygnant");
        const soinMatifiantHomme = produitsData.find(x => x.nom === "Soin Matifiant Homme");

        if (cremeAntiPollution)
            produits.push(cremeAntiPollution);

        if (gelNettoyantOxygenant)
            produits.push(gelNettoyantOxygenant);

        if (soinMatifiantHomme)
            produits.push(soinMatifiantHomme);

        return await Promise.all(produits);
    }

    async GetAllFilteredProduits(filter?: ProduitFilter): Promise<ProduitDTO[]> {
        let produits: ProduitDTO[] = [];
        let produitsData: ProduitDTO[] = [];
        try {
            produitsData = await this._dynamicConfigurationService.GetDataAsync<ProduitDTO[]>(DynamicConfigurationEnum.ProduitData);
        } catch (e) {
            console.log(e)
        }
        if (!filter || (filter.typePeauCriteria.length === 0 && filter.textureCriteria.length === 0))
            return Promise.resolve(produitsData);

        produits = produitsData.filter(product => {
            if (filter.textureCriteria.length > 0 && filter.typePeauCriteria.length > 0) {
                return filter.textureCriteria.includes(product.texture) && filter.typePeauCriteria.some(typePeau => product.typesPeau.includes(typePeau));
            } else if (filter.textureCriteria.length > 0) {
                return filter.textureCriteria.includes(product.texture);
            } else if (filter.typePeauCriteria.length > 0) {
                return filter.typePeauCriteria.some(typePeau => product.typesPeau.includes(typePeau));
            }
            return true; // Si aucun critre, on retourne tous les produits
        });

        const distinctProduits = produits.filter(
            (produit, i, produits) => produits.findIndex(t => t.id === produit.id) === i
        );

        const textureOrder = Object.values(TextureEnum);
        const produitsSorted = distinctProduits.sort((a, b) => textureOrder.indexOf(a.texture) - textureOrder.indexOf(b.texture));

        return produitsSorted;
    }



}