import React from "react";
import "./assets/styles/index.css";
import {BrowserRouter, Route, Routes} from "react-router-dom";
import {HomeLayout} from "./shared/layouts/HomeLayout";
import {DiagnostiqueFormulaireLayout} from "./shared/layouts/DiagnostiqueFormulaireLayout";
import {ProduitListingLayout} from "./shared/layouts/ProduitListingLayout";
import {NotFoundLayout} from "./shared/layouts/NotFoundLayout";

// dependency injections
import { ProduitManager } from "./features/produits/services/ProduitManager";
import { DynamicConfigurationService } from "./shared/services/configurations/DynamicConfigurationService";

const App:React.FC = () => {
  return (
      <BrowserRouter>
        <Routes>
          <Route path={"/"} element={<HomeLayout manager={new ProduitManager(new DynamicConfigurationService())} />}></Route>
          <Route path={"/mon-diagnostique"} element={<DiagnostiqueFormulaireLayout />}></Route>
          <Route path={"/produits"} element={<ProduitListingLayout produitManager={new ProduitManager(new DynamicConfigurationService())} configurationService={new DynamicConfigurationService()}/>}></Route>
          <Route path={"*"} element={<NotFoundLayout />}></Route>
        </Routes>
      </BrowserRouter>
  )
};

export default App;