import {TypeChoixEnum} from "../enums/TypeChoixEnum";

export class ChoixDTO {
    private _id: string;
    private _typeChoix: TypeChoixEnum;
    private _libelle: string;
    private _selected: boolean;
    private _nextQuestion?: string;

    constructor(id: string, libelle: string, selected: boolean, typeChoix: TypeChoixEnum) {
        this._id = id;
        this._libelle = libelle;
        this._selected = selected;
        this._typeChoix = typeChoix;
    }

    get id(): string {
        return this._id;
    }

    set id(value: string) {
        this._id = value;
    }

    get typeChoix(): TypeChoixEnum {
        return this._typeChoix;
    }

    set typeChoix(value: TypeChoixEnum) {
        this._typeChoix = value;
    }

    get libelle(): string {
        return this._libelle;
    }

    set libelle(value: string) {
        this._libelle = value;
    }

    get selected(): boolean {
        return this._selected;
    }

    set selected(value: boolean) {
        this._selected = value;
    }

    get nextQuestion(): string | undefined {
        if(this._nextQuestion)
            return this._nextQuestion;
        return undefined;
    }

    set nextQuestion(value: string | undefined) {
        this._nextQuestion = value;
    }
}